/*============================================================
기본 설정
============================================================*/
* {
  outline: none !important;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Noto Sans KR', sans-serif;
}
a {
  display: inline-block;
  color: inherit;
  text-decoration: none;
}
li {
  list-style: none;
  padding: 0;
}
fieldset {
  border: 0;
  padding: 0;
}
legend {
  display: none;
}

/*============================================================
login
============================================================*/
.login_wrap {
  padding: 0;
  margin: 0;
  width: 100%;
  height: 100vh;
  position: relative;
  /* background: linear-gradient(#6baace, #416cb9); */
  background: linear-gradient(
    125deg,
    rgba(164, 224, 255, 1) 0%,
    rgba(107, 170, 206, 1) 100%
  );
  background-attachment: fixed;
  background-repeat: no-repeat;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
}
.login {
  width: 95%;
  max-width: 400px;
  margin: 0 auto;
  font-weight: 400;
  color: #656d9a;
  font-size: 13px;
  padding: 30px;
  background-color: #fff;
  border-radius: 5px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.login_img {
  width: 100%;
  text-align: center;
}
.login_img img {
  width: 20%;
  max-width: 30px;
  margin: 40px 0;
}
.login ul {
  padding: 0;
}
.login a {
  color: #656d9a;
}
.login ul li input {
  display: block;
  width: 100%;
  border-radius: 5px;
  border: 1px solid #e3ebf6;
  padding: 6px 8px;
  margin: 8px 0 20px 0;
}
/* .login ul li input[type='checkbox']{display:inline-block; width: 12px;} */
/* .login .login_ck{display: flex; justify-content: space-between; text-align: right;} */
.login .login_ck {
  display: flex;
  justify-content: space-between;
}
.login .login_ck li {
  display: inline-block;
  margin: 0 0 20px 0;
}
.login .login_bt {
  width: 100%;
  text-align: center;
  background-color: #1761fd;
  border-radius: 5px;
  transition: 0.5s;
}
.login .login_bt:hover {
  background-color: #024dec;
  transition: 0.5s;
}
.login .login_bt a {
  width: 100%;
  color: #fff;
  padding: 10px;
  font-weight: 600;
}

