/*============================================================
My page 기본설정
============================================================*/
.mypage_wrap {
  width: 100%;
  color: #2b2b2b;
}
.mypage_wrap .top_gnb {
  margin-left: 280px;
  width: calc(100% - 280px);
  height: 80px;
  position: fixed;
  top: 0;
  left: 0;
  background: #fff;
  box-shadow: 0 0 20px rgb(89 102 122 / 10%);
  z-index: 1;
}
.mypage_wrap .top_gnb img {
  height: 30px;
  margin: 25px 10px;
}
/* .mypage_wrap .top_gnb .search{display: inline-block; line-height: 80px;} */
.mypage_wrap .top_gnb .member {
  opacity: 0.8;
  line-height: 80px;
  float: right;
  margin: 0 20px 0 0;
  cursor: pointer;
  height: 100%;
}
.mypage_wrap .top_gnb .member:hover {
  opacity: 1;
}
.mypage_wrap .top_gnb .member .username {
  opacity: 1;
  display: inline-block;
  line-height: 80px;
  vertical-align: top;
  position: relative;
}
.mypage_wrap .top_gnb .member .username .member_logout {
  color: #fff;
  display: none;
  position: absolute;
  top: 60px;
  background: #1761fd;
  width: 100%;
  height: 40px;
  line-height: 40px;
  text-align: center;
  border-radius: 15px;
  transition: 0.5s;
}
.mypage_wrap .top_gnb .member .username .member_logout:hover {
  cursor: pointer;
  background: #1761fd;
  transition: 0.5s;
}
.mypage_wrap .top_gnb .member .username .member_logout a {
  width: 100%;
  height: 100%;
}
.mypage_wrap .side_gnb {
  width: 280px;
  position: fixed;
  top: 0;
  height: 100vh;
  font-weight: 700;
  font-size: 16px;
  color: #3b3f5c;
  background-color: #fafafa;
  box-shadow: 0 0 21px 0 rgb(89 102 122 / 10%);
}
.mypage_wrap .side_gnb .logo_bt {
  width: 100%;
  height: 80px;
}
.mypage_wrap .side_gnb .logo_bt a {
  width: 100%;
  height: 80px;
  line-height: 80px;
  font-size: 35px;
  text-align: center;
  color: #1761fd;
  background: #fff;
}
.mypage_wrap .side_gnb .logo_bt p {
  width: 100%;
  height: 80px;
  line-height: 80px;
  font-size: 35px;
  text-align: center;
  color: #1761fd;
  background: #fff;
}
/* .mypage_wrap .side_gnb .logo_bt img{width: 100%; height: 80px; line-height: 80px;} */
.mypage_wrap .side_gnb .content_menu {
  width: 100%;
  padding: 40px 10px;
}
.mypage_wrap .side_gnb .content_menu .active {
  color: #000;
  background: #efefef;
  border: 10px;
}
.mypage_wrap .side_gnb .content_menu li {
  width: 100%;
  height: 30px;
  line-height: 30px;
  margin: 10px 0;
}
.mypage_wrap .side_gnb .content_menu li a {
  width: 100%;
}
.mypage_wrap .side_gnb .content_menu li p {
  display: inline-block;
