  border: 10px;
}
.content {
  min-height: calc(100vh - 80px);
  margin-top: 80px;
  margin-left: 280px;
  background: #f5f6f6;
  padding: 20px;
  color: #333;
}
.content .content_box {
  display: inline-block;
  margin: 10px;
  vertical-align: top;
  background: #fff;
