@media (max-width: 1435px) {
  .content .content_box {
    display: block;
    width: 98%;
  }
}
@media (max-width: 880px) {
  .mypage_wrap .top_gnb img {
    height: 20px;
    margin: 30px 5px;
  }
  .mypage_wrap .top_gnb .member .username {
